.NavBar {
  width: 65%;
  margin: 30px auto;
}

.GaleryContainer {
  width: 80%;
  margin: 30px auto;
}

.SearchTag {
  font-size: 30px;
  margin: 20px 0;
  text-transform: uppercase;
}

.Image {
  cursor: pointer;
}

.GaleryGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
}

.IndividualTag {
  font-weight: 500;
  text-transform: uppercase;
  border: 1px solid grey;
  align-self: flex-start;
  font-size: 12px;
  margin: 10px;
  padding: 8px;
  border-radius: 5px;
  max-height: 26px;
  cursor: pointer;
}

.IndividualTagsContainer {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.ModalImagePreview {
  width: 90%;
  max-height: 650px;
  margin: auto;
  border-radius: 10px;
}

@media (max-width: 600px) {

  .GaleryGrid {
    display: grid;
    grid-template-columns: 100%;
    grid-gap: none;
    margin: none;
  }

  .Image {
    width: 100%;
  }
}
